html{
  font-size: 62.5%;
  box-sizing: border-box;
}
body, input,select,textarea,button{
  font-size: 1.6rem;
  font-family: Arial, Helvetica, sans-serif;
}
body,#root{
  height: 100vh;
  margin: 0%;
}
.grid-container{
  display:grid;
  grid-template-areas: "header"
  "main"
  "footer";
  grid-template-rows: 5rem 1fr 5rem;
  grid-template-columns: 1fr;
  height: 100%;
}
a{
  text-decoration: none;

}
header{
  grid-area: header;
  background-color: #387999;
  display: flex;
  align-items: center;
  padding: 0.5rem;
  
}
header a{
  color: #ffffff;
  text-decoration: none;
}
a:hover{
  color: rgb(255, 68, 0);
}
main{
  grid-area: main;
}
footer{
  grid-area: footer;
  background-color: #387999;
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.content{
  display: flex;
  flex-wrap: wrap;
}
.main{
  flex: 3 60rem;
}
.sidebar{
  flex: 1 20rem;
}
.products{
  display:flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
  list-style-type: none;

}
.products li{
  flex: 0 1 29rem;
  height: 47rem;
  padding:1rem;
  margin: 1rem; 

}


.product{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.product img{
  max-width: 37rem;
  max-height: 37rem;
}
.product_price{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.product_price div,
.product_price button{
  text-align: center ;
  flex: 1;
}

.product_price div{
  font-size: 2rem;
}
.button{
  padding: 1rem;
  border: 0.1rem #c0c0c0 solid;
  background-color: #f0f0f0;
  cursor: pointer;
}
.button:hover{
  border: 0.1rem #808080 solid;

}
.button.primary{
 background-color: #f0c040;
}
.filter-container{
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  margin: 1rem;
  border-bottom: 0.1rem solid #c0c0c0;border-bottom: 0.1rem solid #c0c0c0;


}
.filter-container div{
  flex: 1;
}
.cart{
  display: flex;
  padding: 1.15rem;
  margin: 1rem;
}
.cart-header{
  border-bottom: 0.1rem solid #c0c0c0;
}
.cart-items{
  width: 100%;
  padding: 0%;
  list-style: none;
}
.cart-items img{
  width:5rem
}
.cart-items li{
  display: flex;
  padding: 1rem;
}
.cart-items li div{
  padding: 0.3rem;
}
.cart-items li div:last-child{
  flex:1;
}
.right{
  text-align: right;
}
.total{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.total > div{
  font-size: 2rem;
}
.total > div, 
.total > button{
  flex: 1;
}
form{
  width: 100%;
}
.form-container{
  width: 100%;
  padding: 0;
  list-style-type: none;
}
.form-container li{
  display:flex ;
  flex-direction: column;
  padding: 1rem;
}
input{
  padding: 1rem;
  border: 0.1rem #c0c0c0 solid;
}

/* Product-details*/
.product-details{
  display: flex;
  flex-wrap:wrap;
  justify-content: space-between;
  height: 96vh;
}
.product-details img{
  max-height: 100vh;
  max-width: 46rem;
  margin: 0 auto;
}
.product-details-description{
  flex:1 1;
  margin: 1rem;
}
.close-modal{
  position: absolute;
  right: 1rem;
  top: 2.5rem;
  z-index: 1000;
}

.order-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.order-details li {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.order-details li div {
  padding: 10px;
}
.order-details .items {
  text-align: right;
  padding: 0;
}
.success-message {
  color: green;
}
/* Orders */
.orders {
  width: 100%;
  padding: 10px;
}
tbody > tr:nth-child(odd) {
  background-color: #f0f0f0;
}
td {
  padding: 5px;
}
th {
  text-align: left;
}